.banners {
  @media (max-width: @md) {

    &__inner {
      display: block;
    }

    &__item {
      width: 100%;
      margin: 0 0 2rem 0;
      &:last-child {
        margin: 0;
      }
    }

    &__img {
      max-height: 22rem;

      &::before {
        padding-top: 30%;
      }
    }

  }
}