*,
::after,
::before {
  box-sizing: inherit;
  outline: none;
}

html {
  box-sizing: border-box;
  font-size: @f-root-size;
}

body {
  -webkit-tap-highlight-color: transparent;
  text-rendering: optimizeLegibility;
  font-family: @f-base-family;
  font-size: @f-base-size;
  font-weight: @f-base-weight;
  line-height: @f-base-height;
  word-wrap: break-word;
  color: @c-black;
  overflow-x: hidden;
  overflow-y: auto;
}

a {
  color: #2D74A2;
  transition: color @time;
  text-decoration: underline;

  &:hover {
    text-decoration: none;
  }
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

iframe {
  border: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
  color: #000000;
}

svg {
  fill: currentColor;
}

input[type='text'] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input[type='text']::-ms-clear,
input[type='text']::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type='number'] {
  -moz-appearance: textfield;
}

input,
textarea {
  font: inherit;
  color: inherit;
  background-color: #F0F0F0;
  &::placeholder {
    font: inherit;
  }
}

textarea {
  resize: vertical;
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5 {
  font-weight: bold;
}

h1, .h1 {
  font-size: 4rem;
  line-height: 1.2;
  text-transform: uppercase;
}
h2, .h2 {
  font-size: 2.4rem;
  line-height: 1.16;
  text-transform: uppercase;
}
h3, .h3 {
  font-size: 1.8rem;
  line-height: 1.38;
  text-transform: uppercase;
}
h4, .h4 {
  font-size: 1.6rem;
  line-height: 1.18;
}

._scroll-lock {
  overflow: hidden;
}


