.magazine {
  @media (max-width: @md) {
    &__inner {
      display: block;
    }

    &__banner-wrap {
      max-height: 39.4rem;
    }

    &__left {
      margin: 0 0 2rem 0;
    }

    &__right {
      width: 100%;
    }

    &__banner-wrap {
      &::before {
        content: '';
        display: block;
        width: 100%;
        padding-top: 50%;
      }
    }

    &__slide-img {
      min-height: 10rem;
    }

    &__slide-title {
      font-size: 1.4rem;
    }
  }

  @media (max-width: @sm) {
    &__banner-wrap {
      &::before {
        padding-top: 100%;
      }
    }

    &__slider-inner {
      margin-left: 0;
    }

    &__slider-header {
      padding: 1rem;
      display: block;
    }

    &__slider-title {
      margin: 0 0 1rem 0;
    }

    &__slider-navigation {
      justify-content: space-between;
    }

    &__slide {
      width: 100% ;
    }

    &__slide-img {
      min-height: 14rem;
    }

    &__buttons {
      justify-content: center;
    }

    &__slide-img {
      img {
        object-fit: contain;
      }
    }

  }

}