.banners {
  margin-bottom: 5rem;

  &__inner {
    display: flex;
  }

  &__item {
    width: calc((100% - 2rem) / 2);
    margin-right: 2rem;

    &:last-child {
      margin-right: 0;
    }
  }

  &__img {
    position: relative;
    width: 100%;
    display: block;
    overflow: hidden;

    &::before {
      content: '';
      display: block;
      padding-top: 38%;
    }

    img {
    position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: contain;
      transition: 0.6s;
      transform: scale(0.97);

      &:hover {
        transform: scale(1);

      }
    }

  }

}