.main-news {
  margin-top: -6rem;
  margin-bottom: 5.5rem;

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }

  &__left {
    flex-grow: 1;
    max-width: 77%;
    width: 100%;

  }

  &__right {
    flex-shrink: 0;
    flex-basis: 27.5rem;
    background-color: #FFFFFF;
    box-shadow: 0 0.4rem 1.4rem rgba(0, 0, 0, 0.1);
  }

  &__title-block {
    margin: 0 0 1.5rem 0;
  }

  &__banner {
    width: 100%;
    position: relative;
  }

  &__banner-slider {
    width: 100%;
    overflow: hidden;
    background-color: #EFBB00;
  }

  &__banner-wrap {
    display: flex;
  }

  &__banner-slide {
    flex-shrink: 0;
    width: 100%;
    margin-right: 1rem;
    position: relative;
    &:last-child {
      margin-right: 0;
    }
  }

  &__banner-link {
    display: block;
    position: relative;
    max-height: 44.5rem;
    overflow: hidden;

    &:before {
      content: '';
      display: block;
      width: 100%;
      padding-top: 50%;
    }

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      z-index: 1;
      left: 0;
      border-bottom: 0;
      width: 100%;
      height: 50%;
      background: linear-gradient(356.7deg, #000000 -1.83%, rgba(0, 0, 0, 0) 80%);
      transition: 0.8s;
    }
    &:hover {
      img {
        transform: scale(1.2);
      }
      &::after {
        transition: 0.8s;
        height: 100%;
      }
    }
  }

  &__banner__img {
    display: block;


    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: left center;
      position: absolute;
      top: 0;
      left: 0;
      transition: 0.4s;

    }
  }

  &__banner-content {
    max-width: 74rem;
    position: absolute;
    left: 2.3rem;
    bottom: 3.6rem;
    margin: 0 1rem;
    color: #ffffff;
    z-index: 2;
  }

  &__banner-btn {
    display: flex;
    justify-content: space-between;

    .main-banner__nav {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      &._prev {
        left: 0;
      }
      &._next {
        right: 0;
      }

      svg {
        width: 3rem;
        height: 5rem;
        fill: #ffffa8;
        transition: 0.3s;
        &:hover {
          transform: scale(1.2);
        }
      }

    }
  }

  &__banner-title {
    font-weight: 500;
    color: #FFFFFF;
    margin: 0 0 1.5rem 0;
  }

  &__banner-description {
    font-size: 1.4rem;
    line-height: 1.14;
    color: #FFFFFF;
    margin: 0;
  }

  &__right-title{
    text-transform: none;
    margin: 0 0 1rem 0;
    padding: 1.5rem 1rem 0;
  }

  &__right-item {
    padding: 1.5rem 1rem;
    border-top: 1px solid #F3F3F3;
    border-bottom: 1px solid #F3F3F3;
    &:first-child {
      border-top: none;
    }
  }

  &__right-img {
    position: relative;
    display: block;
    max-height: 10.5rem;
    margin-bottom: 2rem;
    overflow: hidden;

    &:before{
      content: '';
      display: block;
      width: 100%;
      padding-top: 41%;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.4s;
      &:hover {
        transform: scale(1.2);
      }
    }
  }

  &__right-link {
    display: block;
    color: #000000;
    text-decoration: none;
  }

  &__right-date {
    font-size: 1rem;
    line-height: 1.2;
    color: #7D7D7D;
  }

  &__research {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 3.5rem 0 3rem;
    & .main-news__title-block {
      margin: 0;
    }
  }

  &__research-open {
    margin: 0 2rem;
    a {
      color: #EFBB00;
      text-decoration: none;
      font-weight: bold;
      font-size: 1.4rem;
      line-height: 1.14;
      text-transform: capitalize;
    }
  }

  &__content {
    display: flex;
    justify-content: space-between;
  }

  &__slider {
    position: relative;
    border: 1px solid #EFEFEF;
    flex-grow: 1;
    width: 50%;
  }

  &__slider-wrap {
    max-width: 57.3rem;
    width: 100%;
    overflow: hidden;
  }

  &__slide-wrap {
   display: flex;
  }

  &__slide {
    width: 100%;
    flex-shrink: 0;
  }

  &__slide-inner {
   display: flex;
    align-items: center;
  }

  &__slide-img {
    width: 100%;
    max-width: 20.8rem;
    margin-right: 1rem;
    overflow: hidden;
    img {
      width: 100%;
      object-fit: cover;
      transition: 0.4s;

      &:hover {
        transform: scale(1.2);
      }
    }
  }

  &__slide-content {
    margin: 0 2rem;
    max-width: 31.5rem;
  }

  &__nav-btn {
    position: absolute;
    right: 1rem;
    bottom: 1rem;
  }

  &__card {
    max-width: 27.7rem;
    min-width: 20rem;
    width: 100%;
    flex-shrink: 0;
    margin: 0 2rem;
  }

  &__card-link {
    display: block;
  }

  &__card-content {
    position: relative;
  }

  &__card-img {
    display: block;
    position: relative;
    min-height: 29.4rem;
    overflow: hidden;
    &:before {
      content: '';
      display: block;
      width: 100%;
      padding-top: 110%;
    }

    &::after {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
      width: 100%;
      min-height: 9.3rem;
    }

    img {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.4s;

      &:hover {
        transform: scale(1.05);
      }
    }
  }

  &__card-text {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0 1.5rem 2.4rem;
    h4 {
      color: #ffffff;
      text-transform: capitalize;
      margin: 0;
    } 
  }

}