.report {
  @media (max-width: @md) {
    &__inner {
      display: block;
      margin-bottom: 2.5rem;
    }

    &__left {
      width: 100%;
      margin: 0 0 1.5rem 0;
    }

    &__video-img {
      max-height: 25rem;
      img {
        width: auto;
      }
    }

    &__photo-slide {
      width: calc((100% - 1rem * 2) / 2);
    }

    &__video-img {
      &:before {
        padding-top: 40%;
      }
    }
  }

  @media (max-width: @sm) {
    &__photo-slide {
      width: 100%;
    }
  }

}