.report {
  background-color: #0E0E0E;
  color: #FFFFFF;

  &__inner {
    display: flex;
    padding: 5.3rem 0 6.1rem;
    margin-bottom: 5.5rem;
  }

  &__text {
    margin: 0 0 1rem 0;
    text-transform: none;
  }

  .news__date {
    color: #868686;
  }

  &__left {
    width: 50%;
    flex-grow: 1;
    margin-right: 2rem;
  }

  &__photo-head {
   display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2.7rem;
  }

  &__title {
    margin: 0;
    text-transform: none;
  }

  &__carousel {
    & .report__nav {
      svg {
        fill: #FFFFFF;
      }
    }
    & .carousel-nav__line {
      background-color: #FFFFFF;
    }
  }

  &__photo-inner {
  }

  &__photo-slider {
    overflow: hidden;
  }

  &__photo-wrap {
    display: flex;
  }

  &__photo-slide {
    width: calc((100% - 1rem * 2) / 3);
    margin-right: 1rem;
    flex-shrink: 0;

    &:last-child {
      margin-right: 0;
    }

    a {
      color: #FFFFFF;
    }
  }

  &__slide-top {
    display: block;
    width: 100%;
    margin-bottom: 1rem;
    overflow: hidden;

    img {
      width: 100%;
      object-fit: cover;
      transition: 0.4s;

      &:hover {
        transform: scale(1.3);
      }
    }
  }

  &__slide-depiction {
    display: flex;
    margin-bottom: 1.5rem;
  }

  &__slide-img {
    position: relative;
    display: block;
    width: calc((100% - 1rem * 2) / 3);
    max-height: 8.5rem;
    margin-right: 1rem;
    overflow: hidden;

    &:last-child {
      margin-right: 0;
    }

    &:before {
      content: '';
      width: 100%;
      display: block;
      padding-top: 100%;
    }

    img {
      position: absolute;
      top: 0;
      left: 0 ;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.4s;

      &:hover {
        transform: scale(1.15);
      }
    }
  }

  &__right {
    flex-basis: 27.8rem;
  a {
    color: #FFFFFF;
  }
    & .report__title {
      margin-bottom: 2.7rem;
    }
  }

  &__video-link {
    display: flex;
    flex-direction: column;

  }

  &__video-img {
    display: block;
    position: relative;
    width: 100%;
    margin-bottom: 1.5rem;
    overflow: hidden;

    &:before {
      content: '';
      display: block;
      width: 100%;
      padding-top: 79.5%;
    }

    img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.4s;

      &:hover {
        transform: scale(1.2);
      }
    }
  }

}