.header {
  background: #EFBB00;

  &__burger-btn {
    display: none;
    z-index: 11;
  }

  &__top {
    z-index: 10;
    padding: 1.5rem 0;
    border-bottom: 1px solid #FFFFFF;
    background: #EFBB00;
  }

  &__img-link {
    display: block;
  }

  &__top-inner {
    display: flex;
    justify-content: space-between;
  }

  &__wrap-img {
    flex-shrink: 0;
    z-index: 11;
  }

  &__logo-img {
    width: 13.8rem;
  }

  &__list {
    list-style: none;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    max-width: 74rem;
    width: 100%;
  }

  &__item {
    margin: 0 1.5rem;
  }

  &__text {
    font-weight: 500;
    font-size: 1.4rem;
    line-height: 1.14;
    text-transform: capitalize;
    color: #FFFFFF;
    text-decoration: none;
  }

  &__search {
    display: inline-block;
    background-color: #FFFAEA;
    width: 3.5rem;
    height: 3rem;
    svg {
      width: 1.6rem;
      height: 1.6rem;
      fill: #EFBB00;
    }
  }

  &__nav {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__search {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    margin: 0 2rem;
    border: none;
    border-bottom-right-radius: 1.9rem;
    box-shadow: 0px 0px 8px rgba(114, 114, 114, 0.25);
  }

  &__messengers {
    display: flex;
    align-items: center;
  }

  &__messengers-link {
    display: block;
    margin-right: 1rem;
    &:last-child {
      margin-left: 0;
    }
    svg {
      width: 1.9rem;
      height: 1.9rem;
      fill: #FFFFFF;
      opacity: 0.5;
      &:hover {
        opacity: 1;
      }
      &:first-child {
        width: 2.5rem;
        height: 2.5rem;
      }
    }

  }

  &__bottom {
    padding-bottom: 6rem;
  }

  &__bottom-inner {
    display: flex;
    justify-content: space-between;
  }

  &__bottom-items {
    display: flex;
    flex-wrap: wrap;
  }

  &__bottom-item {
    padding: 1.8rem;
    border-left: 1px solid #FFFFFF;
    &:last-child {
      border-right: 1px solid #FFFFFF;
    }
  }

  &__bottom-exit {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    padding: 1rem 2rem;
    border-right: 1px solid #FFFFFF;
    border-left: 1px solid #FFFFFF;
    svg {
      margin-right: 1rem;
      width: 1.6rem;
      height: 1.6rem;
      fill: #FFFFFF;
    }
  }

}
