.news {
  margin-bottom: 5.5rem;

  &__inner {
    display: flex;
    align-items: flex-start;
  }

  &__date {
    display: inline-block;
    font-weight: 500;
    font-size: 1.2rem;
    line-height: 1.16;
    color: #808080;
  }

  &__left {
    flex-grow: 1;
    width: 50%;
    margin-right: 3.4rem;
  }

  &__left-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 2.8rem;
  }

  &__title {
    text-transform: none;
    margin: 0;
  }

  &__link {
    text-decoration: none;
  }

  &__text-link {
    margin: 0;
  }

  &__right {
    flex-shrink: 0;
    flex-basis: 27.8rem;
  }

  &__left-cards {
    display: flex;
    margin-bottom: 5rem;
  }
  
  &__left-card {
    width: calc((100% - 2rem * 2) / 3);
    margin-right: 2rem;

    &:last-child {
      margin-right: 0;
    }

    &--img {
      position: relative;
      display: block;
      width: 100%;
      margin-bottom: 1.5rem;
      overflow: hidden;

      &::before {
        content: '';
        display: block;
        padding-top: 45%;
      }

      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: 0.4s;

        &:hover {
          transform: scale(1.2);
        }
      }
    }
    a {
      text-decoration: none;
    }
    &--title {
      color: #000000;
      margin: 0 0 1rem 0;
    }

  }

  &__left-bottom {
    display: flex;
    align-items: flex-start;
  }

  &__left-banner {
    flex-grow: 1;
    width: 50%;
    margin-right: 2rem;
    position: relative;

    &--img {
      display: block;
      position: relative;
      overflow: hidden;

      &::before {
        content: '';
        display: block;
        width: 100%;
        padding-top: 54%;
      }

      &::after {
        content: '';
        position: absolute;
        bottom: 0;
        min-height: 17.1rem;
        width: 100%;
        background: linear-gradient(356.7deg, #000000 -1.83%, rgba(0, 0, 0, 0) 80%);
      }

      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: 0.4s;

        &:hover {
          transform: scale(1.2);
        }
      }
    }

    &--text {
      position: absolute;
      z-index: 1;
      bottom: 0;
      width: 100%;
      padding: 0 2.5rem 1.5rem 1.5rem;
      h4 {
        color: #FFFFFF;
        margin: 0 0 1rem 0;
      }
      span {
        font-weight: 500;
        font-size: 1.2rem;
        line-height: 1.16;
        color: #FFFFFF;
      }
    }

  }

  &__left-items {
    flex-shrink: 0;
    flex-basis: 27.8rem;
  }

  a {
    text-decoration: none;
  }

  &__left-item {
    padding-bottom: 1.5rem;
    margin-bottom: 2rem;
    border-bottom: 2px dashed #808080;
    &:last-child {
      padding-bottom: 0;
      margin-bottom: 0;
      border-bottom: none;
    }
  }

  &__right-card {
    padding: 0 1.5rem;
    margin-bottom: 2.8rem;
  }

  &__right-title {
    text-transform: none;
    margin: 0 0 2rem 0;
  }

  &__right-img {
    display: block;
    width: 100%;
    margin-bottom: 1.2rem;
    position: relative;
    overflow: hidden;

    &::before {
      content: '';
      display: block;
      width: 100%;
      padding-top: 55.5%;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      max-height: 13.8rem;
      object-fit: contain;
      transition: 0.4s;

      &:hover {
        transform: scale(0.9);
      }
    }
  }

  &__right-subtitle {
    margin: 0 0 1rem 0;
  }

  &__right-date {
    margin-bottom: 2rem;
  }

  &__link {
    display: block;
    text-decoration: none;

    &-text {
      text-align: end;
      margin: 0;
    }
  }



  &__banner-img {
   position: relative;
    display: block;
    width: 100%;
    overflow: hidden;

    &::before {
      content: '';
      display: block;
      padding-top: 108%;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: contain;
      transition: 0.4s;

      &:hover {
        transform: scale(1.2);
      }
    }
  }



}