.header {
  &__burger-btn {
    width: 4rem;
    height: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: transparent;
    border: none;
    position: relative;
    padding: 0;
    cursor: pointer;

    &::before, &::after {
      content: '';
      width: 100%;
      height: 0.4rem;
      background-color: black;
      position: absolute;
      transition: 0.4s;
    }

    &::before {
      top: 0;
    }

    &::after {
      bottom: 0;
    }

    span {
      width: 100%;
      height: 4px;
      background-color: black;
    }



    &._active::before {
      top: 1.3rem;
        transform: rotate(-45deg);
      transition: 0.4s;
    }

    &._active::after {
      bottom: 1.3rem;
      transform: rotate(45deg);
      transition: 0.4s;
    }

    &._active span {
      transform: scale(0);
      transition: 0.4s;
    }

  }
}