.main-news {
  @media (max-width: @md) {
    margin-bottom: 2.5rem;

    &__inner {
      display: block;
    }

    &__left {
      max-width: 100%;
    }

    &__right-img {
      &::before {
        padding-top: 12%;
      }
      img {
        width: auto;
      }
    }

    &__content {
      display: block;
    }

    &__slider {
      width: 100%;
      margin-bottom: 2rem;
    }


    &__research {
      margin: 2rem 0;
    }

    &__slide-content {
      max-width: 100%;
    }

    &__slider-wrap {
      max-width: 100%;
    }

    &__card-text {
      position: relative;
      margin: 0 0 1rem 0;
      h4 {
        color: #000000;
      }
    }

    &__card {
      margin: 0 0 2rem 0;
      max-width: 100%;
      border-bottom: 2px dashed #333333;
    }

    &__card-img {
      max-width: 27.7rem;
      margin: 0 auto 2rem;

      &::after {
        content: none;
      }
    }
  }

  @media (max-width: @sm) {

    &__banner-content {
      margin: 0 3.5rem;
      bottom: 10%;
    }

    &__banner-title {
      font-size: 1.8rem;
      margin: 0 0 1rem 0;
    }

    &__banner-btn {
      .main-banner__nav {
        svg {
          width: 2rem;
          height: 3rem;
        }
      }
    }

    &__banner-link {
      max-height: 100%;
      &:before {
        padding-top: 100%;
      }
    }

    &__banner-content {
      margin: 2rem  ;
      position: relative;
      bottom: 10%;
      left: 0;
    }

    &__banner-description {
      font-size: 1.2rem;
    }

    &__research {
      display: block;
    }

    &__research-open {
      margin: 1rem 0;
    }

    &__slide-inner {
      flex-direction: column;
    }

    &__slide-img {
      margin: 0 0 1rem 0;
    }
  }

}