.header {
  @media (max-width: @md) {

    &__burger-btn {
      display: flex;
    }

    &__top {
      position: fixed;
      width: 100%;
    }

    &__bottom {
      padding-top: 6.6rem;
    }

    &__nav {
      position: absolute;
      background-color: #EFBB00;
      width: 100%;
      top: -1000%;
      left: 0;
      display: block;
      padding: 2rem 0 10rem;
      transition: 0.2s;
      z-index: 10;
      max-height: 100vh;
      overflow: auto;
    }

    &__nav._active {
      top: calc(100% + 1px);
      transition: 0.4s;
    }

    &__list {
      max-width: 100%;
      flex-direction: column;
      align-items: center;
    }

    &__item {
      margin: 0 0 2rem 0;
    }

    &__search {
      display: none;
    }

    &__messengers {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
    }
  }

  @media (max-width: @sm) {
    &__bottom-item {
      padding: 1rem;
    }

    &__bottom-exit {
      border-bottom: 1px solid #FFFFFF;
    }

    &__bottom-inner {
      align-items: start;
    }

    &__bottom-item {
      border: none;
      &:last-child {
        border: none;
      }
    }

  }

}