.news {
margin-bottom: 2rem;

  @media (max-width: @md){
    &__inner {
      display: block;
    }

    &__left {
      width: 100%;
    }

    &__right {
      width: 100%;
    }

    &__right-img {
      max-height: 13.8rem;
    }

    &__banner-img {
      max-height: 30rem;
      &::before {
        padding-top: 46%;
      }
    }

    &__left-bottom {
      display: block;
    }

    &__left-banner {
      width: 100%;
      margin: 0 0 1.5rem 0;
    }

    &__left-items {
      margin-bottom: 1rem;
    }

    &__left-item {
      .news__left-card--img::before {
        padding-top: 20%;
      }
    }

    &__left-card {
      &--img {
        img {
          width: auto;
        }
      }
    }

    &__right-card {
      padding: 0;
      margin-bottom: 2rem;
    }

    &__right-img {
      &::before {
        padding-top: 22.5%;
      }
    }

    &__link {
      &-text {
        text-align:  start;
      }
    }
  }

  @media (max-width: @sm) {
    &__left-cards {
      flex-wrap: wrap;
    }

    &__right-img {
      &::before {
        padding-top: 50%;
      }
    }

    &__left-card {
      width: 100%;
      margin: 0 0 1rem 0;
      &:last-child {
        margin: 0;
      }
    }

    &__banner-img {
     &::before {
       padding-top: 80%;
     }
    }

  }

}