.footer {
  background-color: #F2F2F2;

&__top {
  padding: 5.5rem 0 3rem;
}

  &__top-inner {
    margin: 0 -0.5rem;
   display: flex;
  }

  &__items {
    margin: 0 0.5rem;
    width: calc((100% - 0.5rem * 12) / 6);
  }

  &__list-title {
    font-weight: bold;
    font-size: 1.4rem;
    line-height: 1.14;
    color: #1B1B1B;
    margin-bottom: 1rem;
  }

  &__item {
  }

  &__link {
    font-size: 1.2rem;
    line-height: 2;
    color: #1B1B1B;
    text-decoration: none;
  }

  &__content {
    padding: 2rem 0;
    border-top: 1px solid #E1E1E1;
    border-bottom: 1px solid #E1E1E1;
  }

  &__content-items {
    display: flex;
    justify-content: space-between;
    margin: 0 -2rem;
  }

  &__content-item {
    width: calc(100% / 3);
    padding: 0 2rem;
    border-left: 1px solid #E1E1E1;
    &:first-child {
      border-left: none;
    }
  }

  &__content-logo {
    max-width: 13.8rem;
    margin-bottom: 2.5rem;
    a {
      display: block;
      img {
        object-fit: contain;
        width: 100%;
      }
    }
  }

  &__content-text {
    p {
      font-size: 1.2rem;
      line-height: 1.16;
      margin: 0 0 1.5rem 0;
      &:last-child {
        margin: 0;
      }
      &:not(:first-child) {
        color: #797979;
      }
    }
  }

&__bottom {
  padding: 2rem 0;
}

}