.main-form {
  margin-bottom: 6rem;

  position: relative;
  z-index: 1;

  &__inner {
    padding: 5.5rem 4rem 5.5rem 7.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background:#EFBD00 url("../img/svg/main-form.svg") center center no-repeat;
    background-size: cover;
    border-bottom-right-radius: 7.5rem;
  }

  &__title {
    text-transform: none;
    max-width: 52.2rem;
    margin: 0 0.5rem 0 0;
  }

  &__departure {
    flex-basis: 47.3rem;
    display: flex;
  }

  &__input {
    width: 100%;
    padding: 1.5rem 2rem;
    border: none;
    font-size: 1.6rem;
    line-height: 1.18;
    color: #808080;
  }

  &__button {
    padding: 1.5rem 2.5rem;
    border: none;
    background-color: #000000;
    font-weight: 700;
    font-size: 1.6rem;
    line-height: 1.18;
    color: #FFFFFF;
    cursor: pointer;
  }

}