.magazine {
    margin-bottom: 5.5rem;

    &__inner {
        display: flex;
    }

    &__left {
        flex-basis: 27.9rem;
        flex-shrink: 0;
    }

    &__banner-wrap {
        position: relative;
        width: 100%;
        display: block;
        overflow: hidden;

        &::before {
            content: '';
            display: block;
            width: 100%;
            padding-top: 141%;
        }

        img {
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;
            object-fit: contain;
            height: 100%;
            transition: 0.4s;

            &:hover {
                transform: scale(0.9);
            }
        }
    }

    &__right {
        flex-grow: 1;
        width: 50%;
    }

    &__slider-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: #F2F2F2;
        padding: 1.2rem 2rem;
        margin-bottom: 2.5rem;
    }

    &__slider-navigation {
        display: flex;
    }

    &__slider-title {
        margin: 0;
    }

    &__slider-date {
        margin: 0 2.4rem 0 0;
    }

    &__slider-inner {
        overflow: hidden;
        margin-left: 2rem;
        padding-bottom: 2rem;
        margin-bottom: 2rem;
        border-bottom: 2px dashed #D9D9D9;
    }

    &__slider-wrap {
        display: flex;
        width: 100%;
    }

    &__slide {
        flex-shrink: 0;
        width: calc((100% - 2rem * 2) / 3);
        margin-right: 2rem;
        &:last-child {
            margin-right: 0;
        }
    }

    &__slide-img {
        display: block;
        width: 100%;
        min-height: 15rem;
        position: relative;
        margin-bottom: 2rem;
        overflow: hidden;

        &::before {
            content: '';
            display: block;
            width: 100%;
            position: absolute;
            padding-top: 54%;
        }

        img {
            position: absolute;
            top: 0;
            left: 0;
            object-fit: cover;
            width: 100%;
            max-height: 15rem;
            transition: 0.4s;

            &:hover {
                transform: scale(1.2);
            }
        }
    }

    &__slide-access {
        margin-bottom: 1rem;

        svg {
            margin-right: 1rem;
            width: 1.5rem;
            height: 1.5rem;
            fill: #EFBB00;
        }
    }

    &__slide-view {
        font-weight: 500;
        font-size: 1.2rem;
        line-height: 1.16;
        text-transform: capitalize;
        color: #808080;
    }

    &__slide-link {
        display: block;
        color: #000000;
        text-decoration: none;
    }

    &__slide-title {
        text-transform: capitalize;
        margin: 0;
    }

    &__buttons {
        display: flex;
        justify-content: flex-end;
    }

    &__btn {
        display: block;
        color: #000000;
        text-decoration: none;
        padding: 1.1rem 2.5rem;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
        margin-right: 1.5rem;
        font-weight: 500;
        font-size: 1.6rem;
        line-height: 1.18;
        transition: 0.4s;
        &:last-child {
            margin-right: 0;
        }

        &:hover {
            box-shadow: 0 0 20px rgba(0, 0, 0, 0.25);
        }
    }

}