.footer {
  @media (max-width: @sm) {
    &__top {
      padding: 2rem 0;
    }

    &__top-inner {
      flex-wrap: wrap;
      justify-content: space-between;
    }

    &__items {
      width: calc((100% - 0.5rem * 4) / 2);
      margin: 0 0.5rem 1rem;
      &:last-child {
        margin: 0 0.5rem;
      }
    }
    &__content-items {
      flex-wrap: wrap;
    }

    &__content-item {
      width: 100%;
    }
  }

  &__content-items {
    margin: 0;
  }

}