.container {
  padding-left: @side-offset;
  padding-right: @side-offset;
  width: 100%;

  @media (max-width: @sm) {
    padding-left: @side-offset-sm;
    padding-right: @side-offset-sm;
  }
}

.container-inner {
  width: 100%;
  max-width: @container;
  margin-left: auto;
  margin-right: auto;
}