.carousel-nav__wrap {
  display: flex;
  align-items: center;

   .carousel-nav {
    background: transparent;
    border: none;

    svg {
      display: flex;
      align-items: center;
      cursor: pointer;
      width: 1rem;
      height: 1.5rem;
      fill: #7D7D7D;
    }

    &__line {
      display: inline-block;
      width: 1.5px;
      height: 1.5rem;
      background-color: #000000;
      border-radius: 50%;
    }
     &:not(._disabled):hover {
       svg {
         fill: #EFBB00;
       }
     }
    &._disabled {
      svg {
        opacity: 0.5;
      }
    }
  }

}

