.main-form {
  @media (max-width: @md) {
    &__inner {
      padding: 2rem;
      border-bottom-right-radius: 0;
      display: block;
    }

    &__title {
      max-width: 100%;
      margin: 0 0 1.5rem 0;
    }
  }

  @media (max-width: @sm) {
    &__departure {
      display: block;
    }

    &__title {
      font-size: 1.8rem;
    }


    &__input {
      margin-bottom: 1rem;
    }

    &__button {
      width: 100%;
    }

  }
}